body {
    background-color: #000;
    color: #0f0;
    font-family: 'Courier New', monospace;
  }
  
  .App {
    text-align: center;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .App-header {
    background-color: #000;
    padding: 20px;
    color: #0f0;
  }
  
  h1 {
    font-size: 3em;
    text-shadow: 2px 2px #f0f;
  }
  
  .game-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px;
    flex-grow: 1;
  }
  
  .game-card {
    background-color: #000;
    border: 2px solid #0f0;
    color: #0f0;
    margin: 10px;
    padding: 15px;
    width: 200px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .game-card:hover {
    transform: scale(1.05);
    box-shadow: 0 0 10px #0f0;
  }
  
  .game-card h2 {
    color: #f0f;
  }
  
  .App-footer {
    background-color: #000;
    color: #0f0;
    padding: 10px;
    font-size: 0.8em;
  }
  
  @keyframes blink {
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  .blink {
    animation: blink 1s linear infinite;
    color: #ff0;
  }